<template>
<!--  -->
<div class="dateBox">

  <el-date-picker
    v-model="value2"
    align="right"
    type="date"
    placeholder=""
    :picker-options="pickerOptions1">
  </el-date-picker>

</div>
<!--  -->
</template>

<script>
export default {
    data() {
      return {
        pickerOptions1: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    }
  };
</script>

<style scoped>


</style>



