<template>
<!--  -->
<div class="selectComponents">
  <el-select v-model="value" placeholder="">
    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    </el-option>
  </el-select>
</div>
<!--  -->
</template>

<script>
export default {
  data() {
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: ''
    }
  }
}
</script>

<style scoped>


</style>



